*{
    font-family: 'Courier New', Courier, monospace;

}
body{
    padding: 60px;
    
}
input{
    font-size: 1.2rem;
    color: grey;
    width: 100%;
}
.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}


.card{
   
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
   
    
}
.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 170px;
    object-fit: cover;
    
    
}